.footer {
  padding: @standart-side-padding;
  grid-area: footer;
  align-self: start;

  &--no-icons {
    padding: @footer-top-padding @standart-side-padding;
  }
}

.footer__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__suport {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;

  font-size: 13px;
  color: var(--sub-text-color);
}

.footer__sup-link {
  color: var(--sub-text-color);

  &:hover,
  &:focus {
    color: var(--main-color);
  }
}

